footer {
    background-color: $main-teal-clr1;
    color: $white-clr;

    & .app-footer-container :is(.contact-container pre, .share-copyright-container .copyright p) {
        @include typography(5);
    }

    & .app-footer-container {
        display: flex;

        & .nav-and-contact {
            display: flex;
            justify-content: space-between;

            & .nav-container {
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            & .contact-container {
                display: flex;
                flex-direction: column;
            }
        }

        & .share-copyright-container {
            flex: 1;
            display: flex;

            & nav {
                @include nav-share($size: $size-pic-icon-share-large);
            }
        }
    }
}
