.success-stories-container {
    position: relative;
    background-color: $main-teal-clr3;

    & .success-stories .success-story-preview {
        & p {
            @include typography(5);
        }

        & h3 {
            @include typography(4);
        }

        & h4 {
            @include typography(6);
            font-weight: 500;
            font-style: italic;
        }
    }

    & .bg-pattern {
        position: absolute;

        &.bgPatternHome4 {
            top: 0;
            left: 0;
        }

        &.bgPatternHome5 {
            right: 0;
            bottom: 0;
        }

        &.iconQuotes {
            inset: 0 50%;
            transform: translate(-50%, 0);
            z-index: -1;
            width: max-content;
            height: max-content;
        }
    }

    & .success-stories {
        text-align: center;

        & .title h2 span {
            color: $main-teal-clr4;
        }

        & .success-story-list {
            display: flex;

            & .success-story-preview {
                position: relative;
                z-index: 0;

                & p {
                    padding-block-start: 36px;
                }

                & h3 {
                    padding-block-end: 2px;
                    color: $main-teal-clr4;
                }

                & .avatar-pic {
                    @include avatar($size-pic-avatar-small);
                }
            }
        }
    }
}
